@import "../formul";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.Meeting{
    margin:60px 0px clamp(20px,10.9809vw,150px) 0px;
    position: relative;  
    .gif{
      position: absolute;
      background-color: transparent;
      .img1 {
        @include size($h:100vh,$mh:668px,$minh:668px,$w:100vw);
        mix-blend-mode: difference;
        object-fit: cover;
      }
    }
  .meetingcontent{
    z-index:1000;
    @include displayflex($fd:column,$ai:space-between,$jc:center);
    @include size($w:100%,$h:'',$mw:'',$mh:'',$minh:'');
    @include font($fw:400,$fm:'',$fs:'',$c:'',$ta:'',$lh:'',$ls:'');
    @include mp($m:0px, $p:0px 20px);
 
   

    .meetingtitle{
      @include font($fw:600,$fm:'Montserrat',$fs:clamp(30px, 3.4722vw, 50px),$c:'',$ta:center,$lh:3.2638vw,$ls:''); 
      @include mp($m:0 0 30px 0);

    

    }
    .meeting-text{
      @include font($fw:400,$fm:'Montserrat',$fs:clamp(20px, 1.5277vw, 22px),$c:'',$ta:center,$lh:1.73.61vw,$ls:'');
      @include mp($m:0 0 40px 0,$p:0px clamp(20px,6.9444vw,100px));
      @include size($w:100%,$h:'',$mw:'',$mh:'',$minh:'');
      text-align: center;
      z-index:10;
    }
    .meetingdivs{
      @include displayflex;
      @include mp($m:0px 0px clamp(20px, 6.9444vw,100px) 0px);
      flex-wrap: wrap;
      @media screen and (max-width:682px) {      
        @include displayflex($fd:column)       
      }
      .meetingdiv{
        @include displayflex($fd:column);
        z-index:1000;
        @include mp($m:20px 10px 0px 10px);
       
        
       
     
        .alt{  
          background: #E3E3E3;
          box-shadow: 0px 4px 8px rgba(0,0,0,0.15);
          @include mp($p:0px clamp(25px, 2.5vw, 36px) 0px clamp(25px, 2.9166vw, 42px));
          @include size($w:clamp(300px,27.7777vw, 400px),$h:'',$mw:'',$mh:"",$minh:'');
          // @include media ($d:682px,$w:calc(120px + 40.9238vw));
          // @include font($fw:600,$fm:'Montserrat',$fs:calc(10px + 1.8301vw),$c:'',$ta:left,$lh:'',$ls:'');
          .time{
            @include mp($m: clamp(25px, 2.7777vw, 30px) 0 clamp(25px, 2.7777vw, 30px) 0);
            -webkit-text-stroke: 2px #000000;
            -webkit-text-fill-color: #E3E3E3;
            -webkit-animation: fill 0.5s infinite alternate;
            text-align: left;
            @include font($fw:600,$fm:'Montserrat',$fs:clamp(45px, 4.8611vw, 70px),$c:'',$ta:left,$lh:clamp(50px, 5.9027vw, 85px),$ls:'');
            @media screen and (max-width: 900px){
              -webkit-text-stroke: 1px #000000;
            }
          }
          .rootTitle{
            @include font($fw:600,$fm:'Montserrat',$fs:clamp(20px, 2.2222vw, 32px),$c:'',$ta:left,$lh:clamp(23px, 2.7777vw, 40px),$ls:'');
            margin-bottom: 13px;
            @include size($h:clamp(60px, 8.3333vw, 120px), $mw:'',$mh:'',$minh:'');
          }
          .rootTitleMini{
          @include font($fw:500,$fm:'Montserrat',$fs:clamp(18px, 1.7361vw, 25px),$c:'',$ta:left,$lh:2.0833vw,$ls:'');
          }
          .time-button{
            @include mp($m: clamp(30px, 5.5555vw, 80px) 0 clamp(25px, 2.5694vw, 37px) 0);
            @include displayflex($ai:center,$jc:center);      
            @include font($fw:600,$fm:'Montserrat',$fs: clamp(14px,2.5622vw,35px),$c:'',$ta:left,$lh:'',$ls:'');
            button{
              @include size($w:calc(30px + 14.6412vw),$mw:250px,$h:calc(25px + 1.8301vw),$mh:50px,$minh:'');
              @include font($fw:600,$fm:'Montserrat',$fs: clamp(14px,2.0497vw,22px),$c:#202020,$ta:center,$lh:'',$ls:'');
              box-sizing: border-box;
              outline:none;
              border: none;
              z-index:4;
              background: transparent;
              @include media($d:450px, $w:calc(50px + 14.6412vw), $h:calc(25px + 1.8301vw), $m: 10px 0 0 0);
              // @media screen and (max-width: 500px){
              //   margin-top:40px;
              // }
              &:hover{
                background: transparent;
                color:#202020;
              }
            }





/* -----------2.qaynaq fill----------- */
 
.buttonfx {
  width: 230px!important;
  height: 50px;
  // padding: 0px 48px;
  color: #fff; /* button text color */
  outline: none;
  background: transparent;
  border: none;
  float:right;
  margin-top: clamp(15px, 2.08333vw, 30px);
   @include font($fw:500,$fm:'Montserrat',$fs:clamp(14px,1.5277vw,20px),$c:#333,$ta:center);
  line-height: 2;
  position: relative;
  display: inline-block;
  cursor: pointer;
  text-decoration: none; /* remove underline if using A instead of BUTTON tag */
  overflow: hidden;
  transition: all 0.5s;
  @media screen and (max-width:700px) { 
    width: 150px!important;
    height: 40px;
    padding: 0px 17px;

  }
  
}

/* //// Default effect: Slide from Top  //// */

.buttonfx:before,
.buttonfx:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background: #333; /* onhover background color */
  z-index: -1;
  transform: translate3D(
    0,
    -100%,
    0
  ); /* move elements above button so they don't appear initially */
  transition: all 0.5s;
}

.buttonfx:before {
  background: #fafcd6; /* button default background color */
  z-index: -2;
  transform: translate3D(0, 0, 0);
}

.buttonfx:hover {
  color: white;
}

.buttonfx:hover:after {
  transform: translate3D(0, 0, 0);
  transition: all 0.5s;
}

/* //// Curtain Up  //// */

.curtainup {
  border: 1px solid #333;
  transform-style: preserve-3d;
  width: auto;
}

.curtainup:before,
.curtainup:after {
  transform-origin: center center;
  transform: scale(1, 0);
  width: 101%;
}

.curtainup:hover:before,
.curtainup:hover:after {
  transform: scale(1);
  border-radius: 0;
}











        }
      }
    }
   }
  }
}


