@import "../formul";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.Fond{
    position: relative;
    margin:60px 0px clamp(20px,10.9809vw,150px) 0px;
    @include displayflex($fd: column);
    h1{
    display:block;
    @include font($fw:600,$fm:'Montserrat',$fs:clamp(30px, 3.4722vw, 50px),$c:'',$ta:center,$lh:3.2638vw,$ls:'');  
    margin-bottom:30px;
    z-index:10;
    } 

     .sectionone{
    z-index: 1000;
    position: inherit;
    @include displayflex($fd: row,$ai:flex-start);
    @include mp($p: 2.0463vw, $m:0px 7.3206vw);
    @include media($d:900px, $p: 20px);
    background-color: rgba(227, 227, 227, 0.7); 
    @include size($w: clamp(300px,86.1111vw,1240px), $h:auto);

    @media screen and (max-width: 700px) {
      flex-direction: column;
          @include mp($p: 0px, $m: 0px );

    }

    .text{
      @include displayflex($fd: column);
      // border: 1px solid red;
      @media screen and (max-width: 700px) {
        order: 2;
          @include mp($p: 0px 20px 20px 20px, $m: 0px );
      }
     div{
        @include font($fw:400,$fm:Montserrat,$fs:clamp(14px, 1.4641vw, 20px),$c:'',$ta:'',$lh:23px);
      }
      .second-div{
         @include mp($m:30px 0 0 0);
      }
    }
   img{
     @include mp($m:0 0 0 20px);
      @include size($w:clamp(150px, 40.2777vw, 580px),$h:calc(72px + 19.4444vw),$mw:580px,$mh:352px,$minh:'');
      @media screen and (max-width: 700px) {
        order: 1;
         @include mp($m: 0 0 20px 0px);
         @include size($w:100%,$h:50.2857vw,$mw:1000px,$mh:250px,$minh:'');
      }
   }
  }

  .gif{ 
     position: absolute;
     background-color: transparent; 
     @include size($w:100vw);
     .img1 { 
      @include size($h:100vh,$mh:668px,$minh:668px,$w:100vw);
      mix-blend-mode: difference;  
      object-fit: cover;
     }
  }

 

  
}